import React from 'react';
import {
  Switch,
  Route,
  BrowserRouter,
} from "react-router-dom";
import './App.css';

import Landing from '../src/components/landing';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <div>
          <Switch>
            <Route exact={true} path="/" component={Landing}></Route>
          </Switch>
        </div>
      </BrowserRouter>
    </div>
  );
}

export default App;
