import React, { useState } from "react";
import axios from 'axios';
import { Form, Button, Checkbox } from "semantic-ui-react";
import DatePicker from 'react-datepicker'
import "react-datepicker/dist/react-datepicker.css";
import { Line } from 'react-chartjs-2';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Grid, Segment } from 'semantic-ui-react'


export default function DataForm() {

    const [start_date, setStartDate] = useState();
    const [end_date, setEndDate] = useState();
    const [covidCases, setCovidCases] = useState([]);
    const [covidAdmissions, setCovidAdmissions] = useState([]);
    const [covidDeaths, setCovidDeaths] = useState([]);
    const [dates, setDates] = useState([]);
    const [casesMedian, setCasesMedian] = useState();
    const [admissionsMedian, setAdmissionsMedian] = useState();
    const [deathsMedian, setDeathsMedian] = useState();
    const [casesMean, setCasesMean] = useState();
    const [admissionsMean, setAdmissionsMean] = useState();
    const [deathsMean, setDeathsMean] = useState();

    function getAllData() {
        if (start_date && end_date) {
            axios.get('/api/getData', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                let casesArray = response.data.map((item) => {
                    return item.cases;
                })
                setCovidCases(casesArray)
                let admissionsArray = response.data.map((item) => {
                    return item.admissions;
                })
                setCovidAdmissions(admissionsArray)

                let deathsArray = response.data.map((item) => {
                    return item.deaths;
                })
                setCovidDeaths(deathsArray)

                let datesArray = response.data.map((item) => {
                    return item.date;
                })
                setDates(datesArray)

            });
        } else {
            axios.get('/api/getAllData', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                let casesArray = response.data.map((item) => {
                    return item.cases;
                })
                setCovidCases(casesArray)
                let admissionsArray = response.data.map((item) => {
                    return item.admissions;
                })
                setCovidAdmissions(admissionsArray)

                let deathsArray = response.data.map((item) => {
                    return item.deaths;
                })
                setCovidDeaths(deathsArray)

                let datesArray = response.data.map((item) => {
                    return item.date;
                })
                setDates(datesArray)

            });
        }

    }

    function getMean() {
        if (start_date && end_date) {
            axios.get('/api/getMean', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                console.log(response)
                setCasesMean(response.data.cases)
                setAdmissionsMean(response.data.admissions)
                setDeathsMean(response.data.deaths)
            });
        } else {
            axios.get('/api/getAllMeans', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                console.log(response)
                setCasesMean(response.data.cases)
                setAdmissionsMean(response.data.admissions)
                setDeathsMean(response.data.deaths)
            });
        }

    }

    function getMedian() {
        if (start_date && end_date) {
            axios.get('/api/getMedian', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                console.log(response)
                setCasesMedian(response.data.cases)
                setAdmissionsMedian(response.data.admissions)
                setDeathsMedian(response.data.deaths)
            });
        } else {
            axios.get('/api/getAllMedian', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                console.log(response)
                setCasesMedian(response.data.cases)
                setAdmissionsMedian(response.data.admissions)
                setDeathsMedian(response.data.deaths)
            });
        }

    }

    var values = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit"
    };
    return (
        <div className="row" >
            <Grid columns={3} divided>
                <Grid.Row stretched>
                    <Grid.Column>
                        <Form>

                            <Form.Group widths='equal'>

                                <Form.Group widths='equal'>
                                    <DatePicker
                                        selected={start_date}
                                        onChange={date => setStartDate(date)}
                                    /><DatePicker
                                        selected={end_date}
                                        onChange={date => setEndDate(date)}
                                    />
                                </Form.Group>

                            </Form.Group>

                            <Form.Group widths='equal'>
                                <Checkbox id="allData" label='All Data' />
                                <Checkbox id="mean" label='Mean' />
                                <Checkbox id="median" label='Median' />
                            </Form.Group>
                            <Form.Field>
                                <Button

                                    onClick={(event) => {
                                        event.preventDefault();
                                        if (document.getElementById("allData").checked) {
                                            getAllData();
                                        }
                                        if (document.getElementById("mean").checked) {
                                            getMean();
                                        }
                                        if (document.getElementById("median").checked) {
                                            getMedian();
                                        }
                                    }
                                    }
                                >
                                    Submit
                                </Button>

                            </Form.Field>
                        </Form>
                    </Grid.Column>
                    <div style={{ width: "50%" }}>
                        <Grid.Column >
                            <Line
                                data={{
                                    labels: dates,
                                    datasets: [
                                        {
                                            label: 'Active Cases',
                                            // fill: false,
                                            // lineTension: 0.5,
                                            backgroundColor: 'rgba(75,192,192,1)',
                                            borderColor: 'rgba(75,192,192,1)',
                                            borderWidth: 2,
                                            data: covidCases
                                        },
                                        {
                                            label: 'Admissions',
                                            // fill: false,
                                            // lineTension: 0.5,
                                            backgroundColor: 'rgba(255, 255, 88, 1)',
                                            borderColor: 'rgba(255, 255, 88, 1)',
                                            borderWidth: 2,
                                            data: covidAdmissions
                                        },
                                        {
                                            label: 'Deaths',
                                            // fill: false,
                                            // lineTension: 0.5,
                                            backgroundColor: 'rgba(255, 0, 0, 1)',
                                            borderColor: 'rgba(255, 0, 0, 1)',
                                            borderWidth: 2,
                                            data: covidDeaths
                                        }
                                    ]
                                }}
                            />
                        </Grid.Column>
                    </div>

                    <Grid.Column>
                        <Grid.Row columns={2}>
                        <p>Mean</p>
                        <p>Cases:{casesMean}</p>
                        <p>Admissions:{admissionsMean}</p>
                        <p>Deaths:{deathsMean}</p>
                        </Grid.Row>

                        <Grid.Row columns={3} >
                        <p>Median</p>
                        <p>Cases:{casesMedian}</p>
                        <p>Admissions:{admissionsMedian}</p>
                        <p>Deaths:{deathsMedian}</p>
                        </Grid.Row>
                    </Grid.Column>
                </ Grid.Row>
                {/* <Grid.Row columns={2}>

                </Grid.Row>

                <Grid.Row columns={3} style={{ width: "50%" }}>
                    

                </Grid.Row> */}

            </Grid>


        </div>
    );
}
